import { useState, useEffect } from 'react';
import { Link, useParams, useLocation } from 'react-router-dom';
import { getMoviesbyActors } from 'services/themoviedbAPI';
import { useTranslation } from 'react-i18next';

import { Loader } from 'components/Loader/Loader';
import ImageErrorView from 'components/ImageErrorView/ImageErrorView';
import { ActorsMoviesItem } from 'modules/ActorsMoviesItem/ActorsMoviesItem';
import { ActorMoviesList, MovieListItem } from './ActorsMovies.styled';

//  /actors/:id/
const ActorsMovies = ({ lng }) => {
  const [movies, setMovies] = useState([]);
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState(false);
  const { t } = useTranslation();
  // –∑–≤–µ—Ä—Ç–∞—î–º–æ—Å—å –¥–æ –¥–∏–Ω–∞–º—ñ—á–Ω–æ—ó —á–∞—Å—Ç–∏–Ω–∏ –∞–¥—Ä–µ—Å–∏ —ñ –æ—Ç—Ä–º—É—î–º–æ –∫–ª—é—á –¥–ª—è –ø–æ–¥–∞–ª—å—à–æ–≥–æ –æ—Ç—Ä–∏–º–∞–Ω–Ω—è –¥–∏–Ω–∞–º—ñ—á–Ω–∏–π –¥–∞–Ω–∏—Ö
  const { personId } = useParams();
  //console.log(personId);

  const location = useLocation();

  useEffect(() => {
    (async () => {
      try {
        setIsLoading(true);
        setError(false);

        const dataMovies = await getMoviesbyActors(personId, lng);
        //console.log(dataMovies);

        setMovies(dataMovies);
      } catch (error) {
        setError(error);
      } finally {
        setIsLoading(false);
      }
    })();
  }, [personId, lng]);

  if (!movies) {
    return <div>{t('loading')}</div>;
  }

  if (error) {
    return <p>{error.message}</p>;
  }

  return (
    <>
      {isLoading && <Loader />}
      {error && <ImageErrorView message={t('moviesPage.mistake')} />}

      {!error && (
        <ActorMoviesList>
          {movies.map(movie => (
            <MovieListItem key={movie.id}>
              <Link to={`/movies/${movie.id}`} state={{ from: location }}>
                <ActorsMoviesItem key={movie.id} movie={movie} lng={lng} />
              </Link>
            </MovieListItem>
          ))}
        </ActorMoviesList>
      )}
    </>
  );
};

export default ActorsMovies;

